$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.search-dropdown-profiles {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;

  @media screen and (max-width: 722px) {
    align-items: center;
    flex-direction: column;
  }

  &__search-dropdown {
    width: 43.5%;
    height: 10rem;
    margin-right: 2.2rem;
    margin-top: 2.75rem;

    @media screen and (max-width: 722px) {
      width: 60%;
      margin-right: 0;
    }

    @media screen and (max-width: 400px) {
      width: 100%;
    }
  }

  &__search-dropdown-container {
    height: 7.3rem;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 1.25rem;
  }

  &__profiles {
    width: 41%;
    margin-top: 2.75rem;

    @media screen and (max-width: 722px) {
      width: 60%;
    }

    @media screen and (max-width: 400px) {
      width: 90%;
    }
  }

  &__profiles-container {
    display: flex;
    justify-content: center;
    margin-top: 1.8rem;

    &:nth-child(3) {
      margin-top: 1.5rem;
    }
  }
}
