$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.arrow-buttons {
  display: flex;
  justify-content: space-between;
  height: 3.125rem;
  width: 7.8125rem;

  &_width {
    &_greatest {
      width: 100%;
    }
  }

  &:hover {
    .ripple .arrow-buttons__right-arrow {
      animation: slide-right 1s ease-in-out infinite;
    }

    .ripple .arrow-buttons__left-arrow {
      animation: slide-left 1s ease-in-out infinite;
    }
  }

  &__button {
    @include outline;
    
    width: 3.2rem;
    border: 0.1875rem solid $theme-primary-main;
    border-radius: 50%;
    height: 3.2rem;
    cursor: pointer;

    // for ripple-effect:
    overflow: hidden;
    position: relative;
    transition: background-color 0.5s;

    &:hover {
      background-color: $theme-primary-main;

      .arrow-buttons__left-arrow, .arrow-buttons__right-arrow {
        border-color: $base-secondary-main;
      }
    }

    &_disabled {
      background-color: $base-primary-light;
      border-color: $base-primary-light;
      cursor: default;

      .arrow-buttons__left-arrow, .arrow-buttons__right-arrow {
        animation: none;
        border-color: $base-secondary-main;
        transform: rotate(45deg);
      }

      &:hover {
        background-color: $base-primary-light;
        border-color: $base-primary-light;
      }
    }
  }

  &__left-arrow, &__right-arrow {
    width: 0.84375rem;
    height: 0.84375rem;
    margin: 0.84375rem auto;
    transform: rotate(45deg);
  }

  &__right-arrow {
    border-right: 0.21875rem solid $theme-primary-main;
    border-top: 0.21875rem solid $theme-primary-main;
    margin-right: 0.75rem;
  }

  &__left-arrow {
    border-bottom: 0.21875rem solid $theme-primary-main;
    border-left: 0.21875rem solid $theme-primary-main;
    margin-left: 0.75rem;
  }
}
