$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.header {
  border: 0.2rem solid $text-primary-main;
  box-sizing: border-box;
  height: 4rem;

  @media screen and (max-width: 600px) {
    height: 6rem;
  }

  &_color {
    &_orange-red {
      background-color: $theme-secondary-main;
    }

    &_cyan {
      background-color: $theme-primary-main;
    }
  }

  &__name {
    border-bottom: 0;
    border-right: 0.2rem solid $text-primary-main;
    box-sizing: border-box;
    color: $base-secondary-main;
    float: left;
    font-size: 2rem;
    height: 100%;
    line-height: 4rem;
    text-align: center;
    width: 12rem;

    @media screen and (max-width: 600px) {
      border: 0;
      float: none;
      height: 60%;
      width: 100%;
    }
  }

  &__navigation {
    align-items: center;
    display: flex;
    float: left;
    height: 100%;
    justify-content: space-around;
    width: calc(100% - 12rem);

    @media screen and (max-width: 600px) {
      border-top: 0.2rem solid $text-primary-main;
      float: none;
      height: 40%;
      width: 100%;
    }
  }

  &__link {
    @include outline;

    color: $base-secondary-main;
    font-size: 1.5rem;
    letter-spacing: 0.3rem;
    text-decoration: none;
    padding: 0.15rem 0 0 0.25rem;

    @media screen and (max-width: 500px) {
      letter-spacing: 0;
    }

    @media screen and (max-width: 400px) {
      font-size: 1.3rem;
    }

    &:hover {
      color: $base-primary-main;
    }
  }
}
