$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.help-form {
  position: relative;

  &__header {
    @include outline;

    border: 0;
    background-color: $text-primary-main;
    border-radius: 0.2rem;
    color: $base-secondary-main;
    cursor: pointer;
    font-size: 1.5rem;
    height: 3.125rem;
    line-height: 3.125rem;
    float: right;
    text-align: center;
    vertical-align: center;
    width: 12rem;
  }

  &__arrow {    
    width: 1rem;
    height: 1rem;
    display: inline-block;
    vertical-align: middle;
    border-style: solid;
    border-width: 0.2rem 0.2rem 0 0;
    border-color: $base-secondary-main;
    transform: rotate(-45deg);
    margin-right: 1rem;
    transition: transform 0.5s;

    &_direction {
      &_down {
        transform: rotate(135deg) translate(-0.3rem, 0.3rem);
      }
    }
  }

  &__message-box {
    clear: both;

    &_invisible {
      display: none;
    }
  }
}
