$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.ui-kit-demo {
  &__header {
    margin-top: 1rem;
  }

  &__components-container {
    width: 35.5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 8.8rem auto 6rem;

    @media screen and (max-width: 1600px) {
      width: 40%;
    }

    @media screen and (max-width: 1366px) {
      width: 50%;
    }

    @media screen and (max-width: 1094px) {
      width: 70%;
    }

    @media screen and (max-width: 722px) {
      width: 90%;
    }
  }

  &__buttons {
    width: 72%;
  }

  &__arrows {
    width: 67%;
    margin-top: 2.1rem;

    @media screen and (max-width: 1094px) {
      margin-top: 2.3rem;
    }
  }

  &__charts {
    margin-top: 2.9rem;
  }

  &__sliders {
    width: 88%;
    margin-top: 1.15rem;
  }

  &__stages-box {
    width: 84%;
    margin-top: 2.3rem;
  }

  &__form-elements {
    width: 87%;
    margin-top: 2.7rem;
  }

  &__search-dropdown-profiles {
    width: 97%;
    margin-top: 1.4rem;
  }

  &__news-events {
    width: 90%;
    margin-top: 5.85rem;

    @media screen and (max-width: 1094px) {
      width: 105%;
    }

    @media screen and (max-width: 400px) {
      width: 95%;
    }
  }

  &__location {
    width: 87%;
    margin-top: 3rem;
  }

  &__calendar-messaging {
    width: 91%;
    margin-top: 0.75rem;
  }

  &__video-box {
    width: 87%;
    margin-top: 1.5rem;
    margin-right: 0.2rem;
  }
}
