$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.form-elements {
  &__container {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 2.8rem;

    @media screen and (max-width: 722px) {
      align-items: center;
      flex-direction: column;
    }
  }

  &__message-form {
    width: 18rem;
    margin-top: 0.15rem;

    @media screen and (max-width: 722px) {
      width: 70%;
      margin-bottom: 1.5rem;
    }

    @media screen and (max-width: 400px) {
      width: 100%;
    }
  }

  &__toggles-tick-boxes {
    width: 8rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    margin-left: 6.375rem;

    @media screen and (max-width: 722px) {
      width: 90%;
      flex-flow: row wrap;
      margin-left: 0;
      margin-top: 1rem;
    }

    @media screen and (max-width: 400px) {
      width: 100%;
      flex-direction: column;
    }
  }

  &__toggles {
    width: 100%;

    @media screen and (max-width: 722px) {
      width: 8rem;
    }
  }

  &__toggles-container {
    width: 103%;
    display: flex;
    justify-content: space-between;
    margin-top: 1.875rem;
    position: relative;
    right: 0.3125rem;

    @media screen and (max-width: 722px) {
      width: 100%;
    }
  }

  &__tick-boxes {
    margin-right: 0.3125rem;
    margin-top: 2.5rem;

    @media screen and (max-width: 722px) {
      width: 8rem;
      margin: 0;
    }

    @media screen and (max-width: 400px) {
      margin-top: 2rem;
    }
  }

  &__tick-boxes-container {
    width: 3.9375rem;
    display: flex;
    justify-content: space-between;
    margin: 1.8rem auto 0;
    position: relative;
    right: 0.3125rem;
  }
}
