$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

@import '~jquery-ui/themes/base/selectmenu';

.dropdown {
  &__options {
    display: none;
  }

  + .ui-selectmenu-button.ui-button {
    @include outline;
    
    width: 100%;
    height: 1.75rem;
    background-color: $base-primary-light;
    border-radius: 0.25rem;
    position: relative;
    transition: box-shadow 0.3s;
    cursor: pointer;

    &:hover {
      box-shadow: 0 0 0.3125rem #000;
    }

    .ui-selectmenu-text {
      color: $base-primary-main;
      font-family: 'Source Sans Pro', Arial, sans-serif;
      font-size: 0.9375rem;
      letter-spacing: 0.00875rem;
      padding-left: 0.75rem;
      padding-top: 0.25rem;
    }

    .ui-selectmenu-icon {
      @include outline;
      
      width: 2.0625rem;
      height: 1.75rem;
      background-color: $theme-primary-main;
      border: 0;
      border-radius: 0 0.25rem 0.25rem 0;
      float: right;
      position: relative;

      &:hover {
        box-shadow: 0 0 0.3125rem #000;
      }

      &:active {
        box-shadow: none;
      }

      &::after {
        content: '';
        width: 0.5rem;
        height: 0.5rem;
        border-bottom: 0.125rem solid $base-secondary-main;
        border-left: 0.125rem solid $base-secondary-main;
        margin-top: 20%;
        transform: rotate(-45deg);
        position: absolute;
        left: 35%;
      }
    }
  }
}

.ui-selectmenu-menu.ui-selectmenu-open {
  background-color: $theme-primary-main;
  color: $base-secondary-main;
  border-radius: 0.2rem;
  padding: 0;
  margin: 0;
  cursor: pointer;

  .ui-menu {
    list-style: none inside;
    padding: 0 0 0.5rem;
    margin: 0;
    font-size: 0.9rem;

    .ui-menu-item-wrapper {
      padding: 0.4rem 0 0.35rem 0.8rem;
      letter-spacing: -0.015rem;

      &:hover {
        background-color: $base-primary-main;
        border-radius: 0.2rem;
      }
    }
  }
}

.ui-state-disabled {
  display: none;
}
