$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.user-profile {
  min-width: 15rem;
  max-width: 23rem;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;

  &__tooltip {
    border-radius: 0.5rem;
    text-align: center;
    margin-left: 0.7rem;
    padding: 0 1.35rem;
    position: relative;

    &::after {
      content: '';
      width: 0.625rem;
      height: 0.625rem;
      border-radius: 0.125rem;
      border-width: 0.375rem;
      margin-top: -0.375rem;
      position: absolute;
      right: calc(100% - 0.34375rem);
      top: calc(50% + 0.1875rem);
      transform: rotate(45deg);
    }

    &_color {
      &_cyan {
        background-color: $theme-primary-main;

        &::after {
          background-color: $theme-primary-main;
        }
      }

      &_gray {
        background-color: $base-primary-light;

        &::after {
          background-color: $base-primary-light;
        }
      }
    }
  }

  &__name {
    display: block;
    font-size: 1.125rem;
    line-height: 1.125rem;
    margin-bottom: 0.25rem;
    margin-top: 0.75rem;

    &_color {
      &_orange-red {
        color: $theme-secondary-main;
      }

      &_white {
        color: $base-secondary-main;
      }
    }
  }

  &__job {
    color: $text-primary-main;
    display: block;
    font-size: 0.6875rem;
    font-weight: 900;
    letter-spacing: 0.034375rem;
    line-height: 0.675rem;
    text-transform: uppercase;

    &_color {
      &_black {
        color: $text-primary-main;
      }

      &_white {
        color: $base-secondary-main;
      }
    }
  }

  &__links-container {
    display: flex;
    justify-content: space-around;
    margin: auto;
  }

  &__link {
    @include outline;
    
    width: 2.1875rem;
    height: 2.1875rem;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    margin: 0.4rem 0.15rem 0.58rem;
    flex-shrink: 0;

    &::after {
      content: '';
      width: 1.875rem;
      height: 1.875rem;
      opacity: 0.5;
      position: absolute;
      top: 0;
      left: 0;
    }

    &_theme {
      &_cyan {
        background-color: rgba(20, 202, 130, 0);
        transition: background-color 1s;

        &:nth-of-type(1) {
          background-image: url('images/fb-gray.png');
        }

        &:nth-of-type(2) {
          background-image: url('images/twitter-gray.png');
        }

        &:nth-of-type(3) {
          background-image: url('images/dribbble-gray.png');
        }

        &:hover {
          background-color: rgba(20, 202, 130, 1);
        }

        &:active {
          background-color: rgba(20, 202, 130, 0);
        }
      }

      &_orange-red {
        background-color: rgba(216, 187, 71, 0);
        transition: background-color 1s;

        &:nth-of-type(1) {
          background-image: url('images/fb-white.png');
        }

        &:nth-of-type(2) {
          background-image: url('images/twitter-white.png');
        }

        &:nth-of-type(3) {
          background-image: url('images/dribbble-white.png');
        }

        &:hover {
          background-color: rgba(196, 186, 186, 0.5);
        }

        &:active {
          background-color: rgba(196, 186, 186, 0);
        }
      }
    }
  }
}
