$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.message-form {
  position: relative;

  &__input {
    margin-bottom: 0.75rem;
    display: flex;
  }

  &__tooltip {
    width: 25%;
    border-radius: 0.25rem;
    display: inline-block;
    font-family: Lato, Arial, sans-serif;
    font-size: 0.625rem;
    letter-spacing: 0.05rem;
    line-height: 1.625rem;
    text-align: center;
    color: $base-secondary-main;
    margin: 0.05rem 0.1rem 0 0.5rem;
    padding-top: 0.09375rem;
    position: relative;
    text-transform: uppercase;
    user-select: none;
    flex-shrink: 0;

    &::after {
      content: '';
      border: 0.25rem solid;
      margin-top: -0.25rem;
      position: absolute;
      right: 100%;
      top: 50%;
    }

    &_color {
      &_orange-red {
        background-color: $theme-secondary-main;

        &::after {
          border-color: transparent $theme-secondary-main transparent transparent;
        }
      }

      &_cyan {
        background-color: $theme-primary-main;

        &::after {
          border-color: transparent $theme-primary-main transparent transparent;
        }
      }
    }

    &_invisible {
      display: none;
    }
  }

  &__tick-boxes {
    float: left;
  }

  &__tick-item {
    display: flex;
    align-items: center;
    margin-top: 0.3125rem;
  }

  &__tick-sign {
    margin-left: 0.625rem;
  }

  &__button {
    float: right;
    letter-spacing: 0.03125rem;
    margin-top: 0.6rem;
  }
}
