$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.map {
  &__map-container {
    height: 12.6875rem;
  }

  &__footer {
    height: 4.3125rem;
    background-color: $theme-secondary-main;

    @media screen and (max-width: 400px) {
      height: 6rem;
    }
  }

  &__footer-sign {
    width: 7.5rem;
    color: $base-secondary-main;
    float: left;
    font-size: 1.875rem;
    font-weight: 300;
    line-height: 3.8rem;
    padding-left: 1.6875rem;
    padding-right: 1.125rem;

    @media screen and (max-width: 600px) {
      padding-left: 0.8rem;
      padding-right: 0.6rem;
    }

    @media screen and (max-width: 400px) {
      line-height: 1.4rem;
      padding: 1rem 0.6rem 0.7rem 0.8rem;
    }
  }

  &__footer-address {
    width: 9.0625rem;
    height: 1.7rem;
    color: $base-secondary-main;
    float: left;
    font-size: 0.75rem;
    font-weight: 900;
    letter-spacing: 0.06rem;
    padding-top: 1.25rem;
    text-transform: uppercase;

    @media screen and (max-width: 400px) {
      float: none;
      padding: 0.8rem;
    }
  }

  &__icon {
    width: 1.5rem;
    height: 2rem;
    background: no-repeat url('images/map-marker.svg');
    float: right;
    margin: 1.375rem 1.2rem 1.4375rem 0;

    @media screen and (max-width: 500px) {
      display: none;
    }

    @media screen and (max-width: 400px) {
      display: block;
    }
  }

  &__button {
    @include outline;

    width: 2rem;
    height: 2rem;
    border: 0;
    background: center no-repeat url('images/map-target.svg');
    cursor: pointer;
    float: right;
    margin: 1.15rem 1.2rem 1.375rem 0;
  }

  &__icon, &__button {
    @media screen and (max-width: 400px) {
      position: relative;
      bottom: 2.5rem;
      margin: 1rem;
    }
  }

  &__map-marker {
    width: 2.5625rem;
    height: 2.9375rem;
    background-color: $theme-secondary-main;
    border: 0.0625rem solid $theme-secondary-main;
    border-radius: 50%;
    display: block;
    position: relative;
    top: -1.5rem;
    left: -1.5rem;

    &::after {
      content: '';
      width: 0;
      height: 0;
      border: 1.3625rem solid transparent;
      border-top: 2.5rem solid $theme-secondary-main;
      border-radius: 0 0 0.625rem 0.625rem;
      position: absolute;
      bottom: -2.78125rem;
      left: -0.09375rem;
    }
  }

  &__map-marker-circle {
    width: 1.0625rem;
    height: 1.0625rem;
    background-color: $base-secondary-main;
    border-radius: 50%;
    position: absolute;
    top: 30%;
    left: 0.75rem;
    z-index: 1;
  }
}
