$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

@import '~jquery-ui/themes/base/core';
@import '~jquery-ui/themes/base/slider';

.slider {
  &__scale {
    width: 100%;
    height: 0.3125rem;
    border-radius: 0.625rem;

    &_color {
      &_gray {
        background-color: $base-primary-light;
      }

      &_green {
        background-color: $theme-primary-main;
      }
    }
  }

  &__range {
    border-radius: 0.625rem;

    &_color {
      &_gray {
        background-color: $base-primary-light;
      }

      &_green {
        background-color: $theme-primary-main;
      }

      &_orange-red {
        background-color: $theme-secondary-main;
      }
    }
  }

  .ui-slider-handle#{&}__handle {
    @include outline;
    
    width: 1.21875rem;
    height: 1.21875rem;
    border-radius: 50%;
    cursor: pointer;
    top: -0.4375rem;

    &_color {
      &_green {
        background-color: $theme-primary-main;
      }

      &_orange-red {
        background-color: $theme-secondary-main;
      }
    }

    &_type {
      &_for-stages {
        display: none;
      }
    }
  }

  &__tooltip {
    width: 2.1875rem;
    height: 1.5rem;
    border-radius: 20%;
    font-size: 0.8125rem;
    line-height: 1.75rem;
    letter-spacing: 0.040625rem;
    position: absolute;
    top: -2.0625rem;
    left: -0.5rem;
    text-align: center;
    user-select: none;

    &::after {
      content: '';
      border: 0.3125rem solid;
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -0.34375rem;
    }

    &_theme {
      &_orange-red {
        background-color: $theme-secondary-main;
        color: $base-secondary-main;

        &::after {
          border-color: $theme-secondary-main transparent transparent transparent;
        }
      }
    }
  }

  &__labels-line {
    display: none;
  }

  .ui-slider-pip#{&}__labels-pip {
    &_type {
      &_for-labels {
        font-size: 0.6875rem;
        letter-spacing: 0.034375rem;
        top: 0.625rem;
      }

      &_for-stages {
        width: 1.8125rem;
        height: 1.8125rem;
        border-radius: 50%;
        font-size: 0.875rem;
        line-height: 1.125rem;
        letter-spacing: 0.04375rem;
        text-align: center;
        top: -0.75rem;
      }
    }

    &_background-color {
      &_gray {
        background-color: $base-primary-light;
      }

      &_orange-red {
        background-color: $theme-secondary-main;
      }

      &_green {
        background-color: $theme-primary-main;
      }
    }

    &_color {
      &_white {
        color: $base-secondary-main;
      }

      &_gray {
        color: #d1d1d1;
      }

      &_dark-gray {
        color: $base-primary-main;
      }
    }
  }

  &__labels-label {
    &_type {
      &_for-stages {
        padding-top: 0.0625rem;
        z-index: 1;
      }
    }
  }
}
