$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.button {
  width: 7.375rem;
  border-radius: 0.25rem;

  &_size {
    &_small {
      width: 4.875rem;
    }
  }

  &_width {
    &_greatest {
      width: 100%;
    }
  }

  &_color {
    &_cyan {
      background-color: $theme-primary-dark;

      &:hover {
        background-color: $theme-primary-dark;
      }
    }

    &_orange-red {
      background-color: $theme-secondary-dark;

      &:hover {
        background-color: $theme-secondary-dark;
      }
    }
  }

  &__button-element {
    @include outline;

    width: 100%;
    height: 2rem;
    font-size: 0.718rem;
    font-weight: 900;
    letter-spacing: 0.01rem;
    padding-top: 0.1rem;
    border-radius: inherit;
    cursor: pointer;
    overflow: hidden;
    text-transform: uppercase;
    transform: translate(0, -9%);
    transition: background 0.5s;
    user-select: none;

    &:active {
      transform: translate(0, 0);
    }

    &_size {
      &_small {
        font-size: 0.6rem;
        height: 1.375rem;
      }
    }

    &_color {
      &_cyan {
        background-color: $base-secondary-main;
        color: $theme-primary-main;
        border: 0.0625rem solid $theme-primary-main;

        &:hover {
          background-color: $theme-primary-main;
          color: $base-secondary-main;
        }
      }

      &_cyan-inverse {
        background-color: $theme-primary-main;
        color: $base-secondary-main;
        border: 0.0625rem solid $theme-primary-main;

        &:hover {
          background-color: $base-secondary-main;
          color: $theme-primary-main;
        }
      }

      &_orange-red {
        background-color: $base-secondary-main;
        color: $theme-secondary-main;
        border: 0.0625rem solid $theme-secondary-main;

        &:hover {
          background-color: $theme-secondary-main;
          color: $base-secondary-main;
        }
      }

      &_orange-red-inverse {
        background-color: $theme-secondary-main;
        color: $base-secondary-main;
        border: 0.0625rem solid $theme-secondary-main;

        &:hover {
          background-color: $base-secondary-main;
          color: $theme-secondary-main;
        }
      }
    }
  }
}
