$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.billboard {
  min-width: 18rem;
  min-height: 13.25rem;
  display: flex;
  border-radius: 0.1875rem;
  color: $base-secondary-main;

  @media screen and (max-width: 400px) {
    min-width: 16.5rem;
  }

  &_background-color {
    &_cyan {
      background-color: $theme-primary-main;
    }

    &_orange-red {
      background-color: $theme-secondary-main;
    }
  }

  &__content {
    width: 67%;
    margin: 0.6875rem 1.25rem 0.3rem;

    @media screen and (max-width: 400px) {
      margin: 0.4rem 0.6rem 0;
    }
  }

  &__image {
    width: 7.4rem;
    background-size: cover;
    border-radius: 0.1875rem 0 0 0.1875rem;
  }

  &__description {
    font: 400 0.6875rem Lato, Arial, sans-serif;
    letter-spacing: 0.034375rem;
    margin-left: 0.125rem;
    margin-bottom: 1.3rem;
  }

  &__date {
    &_type {
      &_event {
        height: 4.375rem;
        margin-bottom: 1.125rem;
        font-weight: 300;
        line-height: 1;
      }

      &_new {
        font-size: 0.65rem;
        font-weight: 900;
        letter-spacing: 0.08rem;
        margin: 0.73rem 0 0.82rem 0.1875rem;
        line-height: 1.3;
        text-transform: uppercase;
      }
    }
  }

  &__event-day {
    display: inline-block;
    font-size: 4.5625rem;
    letter-spacing: -0.1825rem;
  }

  &__event-month {
    display: inline-block;
    font-size: 1.875rem;
    margin-left: 0.7rem;
  }

  &__title {
    &_type {
      &_event {
        font-size: 0.7rem;
        font-weight: 900;
        letter-spacing: 0.07rem;
        line-height: 1.2rem;
        text-transform: uppercase;
      }

      &_new {
        font-size: 1.75rem;
        font-weight: 300;
        line-height: 1.625rem;
        margin-top: 0.7rem;
        margin-left: 0.15rem;
      }
    }
  }

  &__button {
    width: 87%;
    margin: 1.25rem auto;
  }
}
