$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.text-field {
  @include outline;

  height: 1.75rem;
  background-color: $base-primary-light;
  border: 0;
  border-radius: 0.25rem;
  font-family: 'Source Sans Pro', Arial, sans-serif;
  font-size: 0.875rem;
  color: $text-primary-main;
  letter-spacing: 0.00875rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  transition: box-shadow 0.3s;

  &_type {
    &_for-search-bar {
      width: 100%;
      border-radius: 0.25rem 0 0 0.25rem;
      box-sizing: border-box;
      font-size: 0.9rem;
      padding: 0 0 0.125rem 0.8rem;

      &::-webkit-search-cancel-button {
        cursor: pointer;
      }
    }

    &_for-message-form {
      @at-root {
        input#{&} {
          width: 100%;
          height: 1.5rem;
          font-size: 0.9375rem;
          padding-bottom: 0.2125rem;
          padding-left: 0.8125rem;
        }

        textarea#{&} {
          width: 100%;
          height: 5.4rem;
          padding-left: 0.84375rem;
          padding-top: 0.50625rem;
          resize: none;
          box-sizing: border-box;
        }
      }
    }

    &_for-message-box {
      width: 100%;
      height: 5.5rem;
      border: 0.1875rem solid $base-secondary-main;
      padding-left: 1.125rem;
      padding-top: 1rem;
      padding-bottom: 1rem;
      resize: none;
      box-sizing: border-box;
      font-size: 0.85rem;
      font-weight: 700;
    }
  }

  &_theme {
    &_red {
      background-color: $theme-secondary-main;
      color: $base-secondary-main;

      &::placeholder {
        color: $base-secondary-main;
      }
    }
  }
}
