$theme-primary-main: #4eb7a8;
$theme-primary-dark: #46ab9d;

$theme-secondary-main: #e75735;
$theme-secondary-dark: #d54c2c;

$base-primary-main: #888;
$base-primary-light: #e5e5e5;

$base-secondary-main: #fff;
$base-secondary-dark: #fdf4f2;

$text-primary-main: #4f4f4f;

@mixin font-face($font-family, $font, $type) {
  $weight: 400;
  $style: normal;

  @if $type == 'light' {
    $weight: 300;
  } @else if $type == 'bold' {
    $weight: 700;
  } @else if $type == 'black' {
    $weight: 900;
  }

  $font-name: '#{$font}-#{$type}';

  @font-face {
    font-family: $font-family;
    font-style: normal;
    font-weight: $weight;
    src: url('../../assets/fonts/#{$font-name}/#{$font-name}.woff2'),
      url('../../assets/fonts/#{$font-name}/#{$font-name}.woff');
  }
}

@mixin animation($translateX, $scale) {
  transform: translateX($translateX) scale($scale) rotate(45deg);
}

@keyframes slide-right {
  0%,
  100% {
    @include animation(0, 1);
  }

  50% {
    @include animation(4px, 1.2);
  }
}

@keyframes slide-left {
  0% {
    @include animation(0, 1);
  }

  50% {
    @include animation(-4px, 1.2);
  }

  100% {
    @include animation(0, 1);
  }
}

@mixin outline($color: #000) {
  outline: none;
  
  &:focus {
    box-shadow: 0 0 0.3125rem $color;
  }
}

.donut-chart {
  position: relative;
  user-select: none;

  &__percentage {
    width: 100%;
    text-align: center;
    color: #9d9d9d;
    font-size: 2.5rem;
    font-weight: 300;
    letter-spacing: -0.125rem;
    line-height: 2.625rem;
    position: absolute;
    top: 2.4rem;

    @media screen and (max-width: 722px) {
      top: 2.6rem;
    }

    @media screen and (max-width: 400px) {
      top: 2.9rem;
    }

    &_invisible {
      display: none;
    }
  }

  &__animation {
    animation: donut-chart-animation 2s ease-in-out;
    transform: rotate(-90deg);
    transform-origin: center;
    transition: stroke-width 1s;
  }

  &:hover .donut-chart__animation {
    stroke-width: 13%;
  }
}

@keyframes donut-chart-animation {
  from {
    stroke-dasharray: 0;
  }
}
